import Button from "@mui/material/Button";

const ButtonCustom = ({
  modoEditar,
  setModoEditar,
  setPLanillaDatos,
  resetDatos
}) => {
  return (
    <>
      {!modoEditar.activo ? (
        <Button name="enviar" variant="contained" type="submit">
          Enviar
        </Button>
      ) : (
        <>
          <Button
            name="enviar"
            variant="contained"
            color="warning"
            id={modoEditar}
            type="submit"
            onClick={() => console.log(modoEditar)}
          >
            Editar
          </Button>

          <p
            onClick={() => {
              setModoEditar(
                { activo: false, id: "" },
                setPLanillaDatos(resetDatos)
              );
              // console.log("s")
            }}
          >
            Agregar nuevo
          </p>
        </>
      )}
    </>
  );
};

export default ButtonCustom;
