const edit = (todos, modoEditar, nombre, descripcion, estado, urgente) => {
  let ob = "";
  ob = todos.find((a) => a.id == modoEditar.id);
  if (ob != null) {
    ob.nombre = nombre;
    ob.descripcion = descripcion;
    ob.estado = estado;
    ob.urgente = urgente;
  }
};

export default edit;
