import { ButtonGroup, Typography, Box, Button, Paper } from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import SendIcon from "@mui/icons-material/Send";
import { styled } from "@mui/material/styles";
const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
  display: "grid",
  gridTemplateColumns: { xs: "100%", md: "10% 50% 40%" },
  margin: "0.5rem"
}));

const CardsTools = ({
  id,
  name,
  descrip,
  urgente,
  borrarTodos,
  setModoEditar
  // modoEditar
}) => {
  const style = {
    display: "grid",
    gridTemplateColumns: { xs: "100%", md: "20% 40% 40%" },
    margin: "0.5rem"
  };
  const divStyle = {
    display: "flex",
    alignItems: "center",
    justifyContent: { xs: "space-between", md: "space-around" },
    margin: "0.2rem"
  };

  return (
    <Item sx={style}>
      <Box sx={divStyle}>
        <div>
          <strong>ID:{id}</strong>
        </div>
        <div>
          <Typography sx={{ color: "blue" }} variant="subtitle1" component="h5">
            <strong>{name}</strong>
          </Typography>
        </div>
      </Box>

      <Box sx={divStyle}>
        <div>
          <strong>Descripción: </strong>
        </div>
        <div>{descrip}</div>
      </Box>
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between"
        }}
      >
        <div>
          <ButtonGroup
            sx={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "center"
            }}
            disableElevation
            variant="contained"
          >
            <Button
              id={id}
              size="small"
              color="error"
              variant="outlined"
              startIcon={<DeleteIcon color="error" />}
              onClick={(e) => borrarTodos(e.target.id)}
            >
              Del..
            </Button>
            <Button
              id={id}
              size="small"
              variant="contained"
              endIcon={<SendIcon />}
              onClick={(e) => setModoEditar({ activo: true, id: e.target.id })}
            >
              Edit
            </Button>
          </ButtonGroup>
        </div>

        <div>
          <Typography sx={{ color: "red", fontStyle: " italic" }}>
            {" "}
            {urgente && "Urgente"}
          </Typography>
        </div>
      </Box>
    </Item>
  );
};

export default CardsTools;
