import Swal from "sweetalert2";

const detectarError = (e, error, setError, nombre, descripcion, estado) => {
  let sw = 0;
  !nombre.trim()
    ? // error.mensaje = 'Bebe ingresar el Nombre' && setError(true)
      setError(
        { estatus: true, mensaje: "Bebe ingresar el Nombre" },
        sw++,
        e.target[0].focus()
      )
    : !descripcion.trim()
    ? // error.mensaje = 'Bebe ingresar el Descripcion' && (setError(true) , position = 1)
      setError(
        { estatus: true, mensaje: "Bebe ingresar el Description" },
        sw++,
        e.target[2].focus()
      )
    : !estado.trim()
    ? // error.mensaje = 'Bebe ingresar el Estado' && (setError(true)  , position = 2)
      setError(
        { estatus: true, mensaje: "Bebe ingresar el Estado" },
        sw++,
        e.target[3].focus
      )
    : setError(
        { ...error, estatus: false },
        Swal.fire(
          "Tarae Agregada!",
          "La tarea fue agregada con exito!",
          "success"
        )
      );
  return sw;
};

export default detectarError;
