import { FormControlLabel, Checkbox, FormGroup } from "@mui/material";

const CheckBoxCustom = ({ urgente, handleChange }) => {
  return (
    <FormGroup>
      <FormControlLabel
        control={<Checkbox />}
        label="Urgente"
        name="urgente"
        value={urgente}
        onChange={handleChange}
        checked={urgente ? true : false}
      />
    </FormGroup>
  );
};

export default CheckBoxCustom;
