import { Grid, Paper, Alert } from "@mui/material";
import { styled } from "@mui/material/styles";
import Formulario from "./Formulario";
import CardsTools from "./CardsTools";
import { useState, useEffect } from "react";
const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary
}));

const TodoList = () => {
  const [todos, SetTodos] = useState([]);

  const [modoEditar, setModoEditar] = useState({
    activo: false,
    id: ""
  });

  const resetDatos = {
    nombre: "",
    descripcion: "",
    estado: "",
    urgente: false
  };

  const [planillaDatos, setPLanillaDatos] = useState(resetDatos);

  const { nombre, descripcion, estado, urgente } = planillaDatos;

  const borrarTodos = (num) => {
    const lisNew = todos.filter((a) => a.id != num);
    SetTodos(lisNew);
  };

  useEffect(() => {
    if (modoEditar.id != "") {
      let ob = "";
      ob = todos.find((a) => a.id == modoEditar.id);
      setPLanillaDatos(ob);
    }
  }, [modoEditar.id]);
  return (
    <>
      <Grid container spacing={2}>
        <Grid item xs={6}>
          <Item>
            <Formulario
              todos={todos}
              SetTodos={SetTodos}
              planillaDatos={planillaDatos}
              setPLanillaDatos={setPLanillaDatos}
              nombre={nombre}
              descripcion={descripcion}
              estado={estado}
              modoEditar={modoEditar}
              setModoEditar={setModoEditar}
              borrarTodos={borrarTodos}
              resetDatos={resetDatos}
            />
          </Item>
        </Grid>
        <Grid item xs={6}>
          {todos.length > 0 ? (
            todos.map((e) => (
              <CardsTools
                key={e.id}
                id={e.id}
                name={e.nombre}
                descrip={e.descripcion}
                urgente={e.urgente}
                borrarTodos={borrarTodos}
                // editarTodos={editarTodos}
                setModoEditar={setModoEditar}
                modoEditar={modoEditar}
              />
            ))
          ) : (
            <Alert severity="info">
              No se encuentra tareas — Ingrese Tareas!
            </Alert>
          )}
        </Grid>
      </Grid>
    </>
  );
};

export default TodoList;
