import { useState } from "react";
import {
  detectarError,
  pressEnter,
  handleChange,
  plus,
  edit
} from "../funciones/funcionesGeneral";
import MensajeModal from "../components/MensajeModal";
import TextfieldCustom from "../element/TexfieldCustom";
import SelectCustom from "../element/SelectCustom";
import CheckBoxCustom from "../element/CheckBoxCustom";
import ButtomCustom from "../element/ButtonSelectCustom";

const Formulario = ({
  todos,
  SetTodos,
  planillaDatos,
  setPLanillaDatos,
  modoEditar,
  setModoEditar,
  borrarTodos,
  resetDatos
}) => {
  const [open, setOpen] = useState(false);
  const [error, setError] = useState({
    estatus: true,
    mensaje: ""
  });
  pressEnter();
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const { nombre, descripcion, estado, urgente } = planillaDatos;

  const onSubmitSubmit = (e) => {
    e.preventDefault();
    if (detectarError(e, error, setError, nombre, descripcion, estado) === 0) {
      if (modoEditar.activo === false) {
        plus(planillaDatos, SetTodos, todos);
      } else {
        edit(todos, modoEditar, nombre, descripcion, estado, urgente);
      }
      setModoEditar({
        activo: false,
        id: ""
      });
    }
    setPLanillaDatos(resetDatos);

    handleOpen();
  };

  return (
    <>
      <h3>FORMULARIO</h3>
      <form onSubmit={onSubmitSubmit}>
        <TextfieldCustom
          array={[nombre, descripcion]}
          label={["nombre", "descripcion"]}
          handleChange={(e) => handleChange(e, setPLanillaDatos, planillaDatos)}
        />

        <SelectCustom
          estado={estado}
          handleChange={(e) => handleChange(e, setPLanillaDatos, planillaDatos)}
        />

        <CheckBoxCustom
          urgente={urgente}
          handleChange={(e) => handleChange(e, setPLanillaDatos, planillaDatos)}
        />

        <ButtomCustom
          modoEditar={modoEditar}
          setModoEditar={setModoEditar}
          setPLanillaDatos={setPLanillaDatos}
          resetDatos={resetDatos}
        />

        <MensajeModal error={error} open={open} handleClose={handleClose} />
      </form>
    </>
  );
};

export default Formulario;
