import TextField from "@mui/material/TextField";

const TextfieldCustom = ({ array, label, handleChange }) => {
  const styleSingle = { margin: "1rem 0 0.2rem 0" };

  return (
    <>
      {array.map((element, item) => (
        <TextField
          key={item}
          id="outlined-basic"
          label={label[item]}
          variant="outlined"
          fullWidth
          sx={styleSingle}
          name={label[item]}
          value={element}
          onChange={handleChange}
          autoFocus={item > 0 ? false : true}
        />
      ))}
    </>
  );
};

export default TextfieldCustom;
