import { Modal, AlertTitle, Alert, Box } from "@mui/material";

const MensajeModal = ({ error, open, handleClose }) => {
  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      {error.estatus === true ? (
        <Box>
          <Alert severity="error">
            <AlertTitle>{error.mensaje}</AlertTitle>
            Campo es obligatorio —{" "}
            <strong>Esc. para salir de la ventana!</strong>
          </Alert>
        </Box>
      ) : (
        <Box>
          <Alert severity="success">
            <AlertTitle>{error.mensaje}</AlertTitle>
            Tarea agregada <strong>Esc. para salir de la ventana!</strong>
          </Alert>
        </Box>
      )}
    </Modal>
  );
};

export default MensajeModal;
